<template>

<div class="columns is-centered">
    <div class="column is-4">
        <div class="has-text-centered"><i class="ri-lock-2-line ri-3x"></i></div>
        <hr>
        <div class="box">
            <div class="field">
              <label for="" class="label">User</label>
              <div class="control has-icons-left">
                <input type="" placeholder="Me0w" class="input" v-model="user" required>
                <span class="icon is-small is-left">
                  <i class="ri-user-line"></i>
                </span>
              </div>
            </div>
            <div class="field">
              <label for="" class="label">Password</label>
              <div class="control has-icons-left">
                <input type="password" placeholder="*******" class="input" v-model="pwd" required>
                <span class="icon is-small is-left">
                  <i class="ri-lock-line"></i>
                </span>
              </div>
            </div>
            <div class="field">
              <button @click="onLogin" class="button">
                Login
              </button>
            </div>
        </div>
    </div>
</div>

</template>

<script>
    export default {
        data() {
            return {
                user:'',
                pwd:''
            }
        },

       methods: {
           onLogin() {
               this.$axios.post("/admin/login",{user:this.user, password:this.pwd})
               .then(resp => {
                   console.log(resp)

                   if(resp.data.status == 200 && resp.data.data.token==1){
                       window.sessionStorage.token = 1;
                       this.$router.push('/overview');
                   } else {
                       this.$notify('Invalid');
                   }
               })
           }
       },

       created() {
           if(window.sessionStorage.token)this.$router.push('/overview')
        },
    }
</script>